
.notification-icon-container{
  padding:30px;
}


.text-icon-link-close-container{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: #3896de 0px 4px;
  font-size: 14px;
  border-radius: 4px;
  width: 70%;
  padding:20px;
  padding-left: 10px;
  font-family: cursive;
}

.material-icons{
  color: #3896de;
}

.close-button{
  cursor: pointer;
  padding-left: 14px;
}

div[className="test"]:empty {
  display:none;
}

@media all and (max-width: 600px) {
  .text-icon-link-close-container {
    font-size: 9px;
    width: 90%;
  }
}

@media all and (max-width: 420px) {
  .text-icon-link-close-container {
    font-size: 8px;
    width: 80%;
  }
}

@media all and (max-width: 290px) {
  .text-icon-link-close-container {
    font-size: 6.5px;
    width: 100%;
  }
}













